@import url('https://fonts.googleapis.com/css2?family=Caprasimo&display=swap');
.navigation,.ul_nav,.image{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

label{
    color: white;
    font-size: 90%;
    margin-left: 2px;
}
input{
    margin-top: 10px;
}
.bare{
    justify-content:space-around;
    display: flex;
    flex-direction: row;
    position: fixed;
    gap: 10px;
    width: 100%;
    height: 30px;
    background-color: black;
    border-radius: 15px;
}
.range{
    margin: 5px;
    text-align: start;
    
}
.couleur{
    background-attachment:fixed;
    background-position: center;
    background-size: cover;
    background-image: url("https://www.adobe.com/fr/express/create/media_1555ee94cd907a402b8a68274ff355f71ee2f379e.jpeg?width=400&format=jpeg&optimize=medium");
}
.navigation{
    position: fixed;
    background-color: white;
    width: 100%;
    box-shadow: 0.4px 0.9px #f0eaea ;
}
.info_pays{
    width: 120px;
    height: 180px;
    border: 2px solid black;
    padding: 2px;
    border-radius: 5px;
}
.ul_nav{
    list-style: none;
    margin: 10px;
}
.navlink{
    margin-top: 2px;
    margin-right: 20px;
    font-size: 20px;
    color: aqua;
}
.plus{
    width: 120px;
    text-align: center;
    margin: auto;
    border: 10px double white;
    position: absolute;
    left: 40%;
    bottom: 50%;
}
.pays{
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 5px;
    flex-wrap: wrap;
    margin: auto;
}
.blog{
    display: flex;
    flex-direction: column;
    text-align: center;
}
.formm{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin: auto;
    text-align: center;
}
.db{
    border: 2px solid aqua;
    margin: 2px auto;
    width: 500px;
}
.err{
    border: 1px solid red;
}
.erreur{
    color: red;
}
.envoyer{
    width: 80px;
    background-color: aqua;
}
input,textarea,.envoyer{
    margin: 5px;
}
input{
    height: 20px;
}
textarea{
    resize: none;
    padding: 2px;
}
.img_pays{
    width: 100px;
    height: 100px;
}
.titre2{
    position: absolute;
    bottom: 60%;
    text-align: center;
    color: white;
    width: 100%;
}
.voir{
    font-size: 20px;
    font-family: 'Caprasimo', cursive;
    color: white;
    &:hover{
        background-color:green;
    }
}
.img_react{
    width: 50px;
    border-radius: 50px;
}

.image{
    border-radius: 50px;
    width: 150px;
    height: 30px;
    margin: auto;
    margin-top: 2px;
    margin-bottom: 2px;
    padding: 6px;
    background-size: cover;
    background-position: center;
    background-image: url('https://lululataupe.com/images/decouverte/geographie/carte-du-monde-avec-capitales-pays.png');
}
.nom_p{
    font-size: 30px;
    margin: auto;
}
.imag{
    width: 100%;
    background-position: center;
    background-size: cover;
    height: 500px;
    border: 2px solide black;
    background-image: url('https://cdn.futura-sciences.com/cdn-cgi/image/width=1024,quality=60,format=auto/sources/images/screen/290713-ile4-43.jpg');
}
.titre{
    text-align: center;
    font-size: 150%;
    font-family: 'Caprasimo', cursive;
    text-decoration: underline;
}